var DegreeSearch;
(function (DegreeSearch) {
    (function (Controllers) {
        var CollegeController = (function () {
            function CollegeController($scope, degreeService) {
                this.scope = $scope;
                this.degreeService = degreeService;
                this.mainCtl = this.scope.$parent.mainCtl;
                this.colleges = new Array();
            }
            CollegeController.prototype.init = function () {
                if (UCF_DEGREE_SEARCH_ANGULAR.colleges) {
                    this.colleges = UCF_DEGREE_SEARCH_ANGULAR.colleges;
                }

                this.registerRoutes();
                this.addHandlers();
            };

            CollegeController.prototype.addHandlers = function () {
                var _this = this;
                this.scope.$watch('mainCtl.searchQuery', function () {
                    _this.onQueryChange();
                });
                this.scope.$watch('mainCtl.selectedProgramType', function () {
                    _this.onQueryChange();
                });
            };

            CollegeController.prototype.registerRoutes = function () {
                this.mainCtl.routeRegExps.college = new RegExp('\/college\/([a-zA-Z-_]*)\/?');
            };

            CollegeController.prototype.onSelected = function (value) {
                this.mainCtl.selectedCollege = value;
                this.mainCtl.currentPage = 1;
                this.mainCtl.getSearchResults();
                this.onQueryChange();
            };

            CollegeController.prototype.onQueryChange = function () {
                var _this = this;
                this.degreeService.getCollegesCounts(this.mainCtl.searchQuery, this.mainCtl.selectedProgramType, function (response) {
                    _this.updateCounts(response);
                }, function (response) {
                    _this.colleges.forEach(function (college) {
                        college.count = null;
                    });
                });
            };

            CollegeController.prototype.updateCounts = function (response) {
                var counts = response.data;

                this.colleges.forEach(function (college) {
                    if (typeof counts[college.slug] !== 'undefined') {
                        college.count = counts[college.slug];
                    } else {
                        college.count = 0;
                    }
                });
            };
            CollegeController.$inject = ['$scope', 'DegreeService'];
            return CollegeController;
        })();
        Controllers.CollegeController = CollegeController;
    })(DegreeSearch.Controllers || (DegreeSearch.Controllers = {}));
    var Controllers = DegreeSearch.Controllers;
})(DegreeSearch || (DegreeSearch = {}));
