var DegreeSearch;
(function (DegreeSearch) {
    (function (Services) {
        var DegreeService = (function () {
            function DegreeService($http, $location) {
                this.http = $http;
                this.location = $location;
                this.apiUrl = UCF_DEGREE_SEARCH_ANGULAR.remote_path;
                this.limit = UCF_DEGREE_SEARCH_ANGULAR.limit;
            }
            DegreeService.prototype.getDegreeResults = function (query, args, successCallback, errorCallback) {
                var params = {
                    search: query,
                    colleges: args.college,
                    program_types: args.programType,
                    page: args.page ? args.page : 1,
                    limit: args.limit ? args.limit : this.limit
                };

                this.http.get(this.apiUrl + '/degrees', { params: params }).then(function (response) {
                    successCallback(response);
                }, function (response) {
                    errorCallback(response);
                });
            };

            DegreeService.prototype.getProgramTypes = function (successCallback, errorCallback) {
                this.http.get(this.apiUrl + '/program-types').then(function (response) {
                    successCallback(response);
                }, function (response) {
                    errorCallback(response);
                });
            };

            DegreeService.prototype.getProgramTypesCounts = function (query, college, successCallback, errorCallback) {
                var params = {
                    search: query,
                    colleges: college
                };

                params.colleges = params.colleges === 'all' ? '' : params.colleges;

                this.http.get(this.apiUrl + '/program-types/counts', { params: params }).then(function (response) {
                    successCallback(response);
                }, function (response) {
                    errorCallback(response);
                });
            };

            DegreeService.prototype.getColleges = function (successCallback, errorCallback) {
                this.http.get(this.apiUrl + '/colleges').then(function (response) {
                    successCallback(response);
                }, function (response) {
                    errorCallback(response);
                });
            };

            DegreeService.prototype.getCollegesCounts = function (query, programType, successCallback, errorCallback) {
                var params = {
                    search: query,
                    program_types: programType
                };

                params.program_types = params.program_types === 'all' ? '' : params.program_types;

                this.http.get(this.apiUrl + '/colleges/counts', { params: params }).then(function (response) {
                    successCallback(response);
                }, function (response) {
                    errorCallback(response);
                });
            };
            DegreeService.$inject = ['$http', '$location'];
            return DegreeService;
        })();
        Services.DegreeService = DegreeService;
    })(DegreeSearch.Services || (DegreeSearch.Services = {}));
    var Services = DegreeSearch.Services;
})(DegreeSearch || (DegreeSearch = {}));
