var DegreeSearch;
(function (DegreeSearch) {
    (function (Controllers) {
        var MainController = (function () {
            function MainController($scope, $location, degreeService) {
                var _this = this;
                this.scope = $scope;
                this.location = $location;
                this.degreeService = degreeService;
                this.pages = new Array();
                this.routeRegExps = {
                    college: null,
                    program: null,
                    search: null
                };

                setTimeout(function () {
                    _this.init();
                }, 0);
            }
            MainController.prototype.init = function () {
                var _this = this;
                this.registerRoute();
                this.setDefaults();
                this.parsePath();
                this.scope.$watch('mainCtl.searchQuery', function (query) {
                    _this.handleInput(query);
                });
                this.getSearchResults();
            };

            MainController.prototype.getSearchResults = function () {
                var _this = this;
                this.totalResults = null;

                var programType = this.selectedProgramType === 'all' ? '' : this.selectedProgramType;
                var college = this.selectedCollege === 'all' ? '' : this.selectedCollege;

                this.degreeService.getDegreeResults(this.searchQuery, {
                    college: college,
                    page: this.currentPage,
                    programType: programType
                }, function (response) {
                    _this.successHandler(response);
                }, function (response) {
                    _this.errorHandler(response);
                });
            };

            MainController.prototype.successHandler = function (response) {
                this.results = response.data;
                this.totalResults = this.results.totalPosts;
                this.currentPage = this.results.currentPage;
                this.totalPages = this.results.totalPages;
                this.startIndex = this.results.startIndex;
                this.endIndex = this.results.endIndex;
                this.buildLocation();
                this.pagination();
            };

            MainController.prototype.errorHandler = function (response) {
                this.results = {};
            };

            MainController.prototype.handleInput = function (query) {
                this.searchQuery = query;
                this.currentPage = 1;
                this.buildLocation();
                this.getSearchResults();
            };

            MainController.prototype.previousPage = function () {
                this.currentPage--;

                if (this.currentPage < 1) {
                    this.currentPage = 1;
                } else {
                    this.getSearchResults();
                }
            };

            MainController.prototype.nextPage = function () {
                this.currentPage++;

                if (this.currentPage <= this.totalPages) {
                    this.getSearchResults();
                } else {
                    this.currentPage--;
                }
            };

            MainController.prototype.goToPage = function (page) {
                if (page >= 1 && page <= this.totalPages) {
                    this.currentPage = page;
                    this.getSearchResults();
                }
            };

            MainController.prototype.registerRoute = function () {
                this.routeRegExps.search = new RegExp('\/search\/([\\w\\s]*)\/?');
            };

            MainController.prototype.setDefaults = function () {
                var _this = this;
                if (UCF_DEGREE_SEARCH_ANGULAR.default_program_type) {
                    this.selectedProgramType = UCF_DEGREE_SEARCH_ANGULAR.default_program_type;
                }

                if (UCF_DEGREE_SEARCH_ANGULAR.default_college) {
                    this.selectedCollege = UCF_DEGREE_SEARCH_ANGULAR.default_college;
                }

                if (UCF_DEGREE_SEARCH_ANGULAR.enabled_routes) {
                    // Set defaults.
                    this.enabledRoutes = {
                        college: false,
                        program: false,
                        search: false
                    };

                    UCF_DEGREE_SEARCH_ANGULAR.enabled_routes.forEach(function (route) {
                        _this.enabledRoutes[route] = true;
                    });
                } else {
                    this.enabledRoutes = {
                        college: true,
                        program: true,
                        search: true
                    };
                }
            };

            MainController.prototype.parsePath = function () {
                var path = this.location.path();

                if (this.enabledRoutes.college && this.routeRegExps.college) {
                    var matches = this.routeRegExps.college.exec(path);
                    if (matches) {
                        this.selectedCollege = matches[1];
                    }
                }

                if (this.enabledRoutes.program && this.routeRegExps.program) {
                    var matches = this.routeRegExps.program.exec(path);
                    if (matches) {
                        this.selectedProgramType = matches[1];
                    }
                }

                if (this.enabledRoutes.search && this.routeRegExps.search) {
                    var matches = this.routeRegExps.search.exec(path);
                    if (matches) {
                        this.searchQuery = matches[1];
                    }
                }
            };

            MainController.prototype.buildLocation = function () {
                var path = '/';

                if (this.selectedCollege && this.enabledRoutes.college && this.selectedCollege !== 'all') {
                    path += 'college/' + this.selectedCollege + '/';
                }

                if (this.selectedProgramType && this.enabledRoutes.program && this.selectedProgramType !== 'all') {
                    path += this.selectedProgramType + '/';
                }

                if (this.searchQuery && this.enabledRoutes.search) {
                    path += 'search/' + this.searchQuery + '/';
                }

                this.location.path(path);
            };

            MainController.prototype.pagination = function () {
                var pagePad = $(document).innerWidth() < 768 ? 2 : 4;

                var startPage = this.currentPage - pagePad < 1 ? 1 : this.currentPage - pagePad;
                var endPage = this.currentPage + pagePad > this.totalPages ? this.totalPages : this.currentPage + pagePad;

                // Reset the array
                this.pages = new Array();

                for (var i = startPage; i <= endPage; i++) {
                    this.pages.push(i);
                }
            };
            MainController.$inject = ['$scope', '$location', 'DegreeService'];
            return MainController;
        })();
        Controllers.MainController = MainController;
    })(DegreeSearch.Controllers || (DegreeSearch.Controllers = {}));
    var Controllers = DegreeSearch.Controllers;
})(DegreeSearch || (DegreeSearch = {}));
